html {
  font-family: "Open Sans", sans-serif; /* this should be the rest of the output you got from Google Fonts */
  background-color: #181818;
  
}

 
/*html {
  box-sizing: border-box;
}*/

body {
  margin: 0;
  font-size: 25px;
  color: white;
}

.container {
  margin: 1rem;
  text-align: center;
}

.nav {
  background-color: #000000;
  color: white;
  display: flex;
  align-items: stretch;
  gap: 30%;
}

.site-title {
  /*font-size: 2rem;*/
  font-size:2.5vw
}

.nav ul {
  /*font-size: 2rem;*/
  font-size:2vw;
  justify-content: space-evenly;
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 10%;
}

.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0.20rem;
}

.nav li.active {
  /*background-color: #222222;*/
  color: red;
}

.nav li:hover {
  /*background-color: #141414;*/
  color: rgb(141, 48, 48);
}

.welcome-title{
  color: white;
  /*display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;*/
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.enter-button{
  background-color: rgba(255, 0, 0, 0.411);
  text-decoration: none;
  color: inherit;
  padding: 0.30rem;
  position: fixed;
  top: 58%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.splash {
  background-image: url("../bokeh.jpg");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}
